
.note-text {
  width: 97%;
  margin: auto;
}
textarea {
  width: 97%;
  border: none;
  font-size: 1rem;
  color: #4a4a4a;
  line-height: 1.5;
  resize: none;
  height: 100%;
  outline: none;
  margin-left: 1.5%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
h1 {
  font-size: 2em;
}
h2 {
  font-size: 1.5em;
}
h3 {
  font-size: 1.17em;
}
h4 {
  font-size: 1.12em;
}
h5 {
  font-size: .83em;
}
h6 {
  font-size: .75em;
}



